import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteDataType } from '@core/services/route.service';

const routes: Routes = [
	{
		path: '',
		loadChildren: () => import('./pages/ad-detail/ad-detail-page.module').then((m) => m.AdDetailPageModule),
		data: {
			[RouteDataType.PAGE_REFERENCE]: 'ad-detail',
			runGuardsAndResolvers: 'always',
		},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class AdDetailPageRoutingModule {}
